div.App {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

div.App {
    --panel-width: 320px;
}

div.App > div.panel {
    position: absolute;
    left: 0;
    top: 0;
    width: var(--panel-width);
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
}

div.App > div.view {
    position: absolute;
    top: 0;
    left: var(--panel-width);
    height: 100%;
    width: calc(100% - var(--panel-width));
    overflow: hidden;
}

div.App > div.view > canvas {
    position: absolute;
    top: 0;
    left: 0;
    height: 70%;
    width: 100%;
}

div.App > div.view > div {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 30%;
    width: 100%;
    overflow: auto;
}

div.App > div.panel > header {
    height: 2rem;
    flex: 0 0 auto;
    z-index: 1;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

div.App > div.panel > header > div {
    padding: 0 .5rem;
}

